@import url(https://fonts.googleapis.com/css2?family=Mulish:wght@400;600;700;900&display=swap);
@import url(https://fonts.googleapis.com/css?family=Open+Sans+Condensed:300);
/* Imports */

/* Variables */

:root {
	--main-bg-color: #ffffff;
	--primary-bg-color: #4f437c;
	--primary-txt-color: #ffffff;
	--secondary-bg-color: #e44f50;
	--select-bg-color: #86efea;
	--secondary-txt-color: #ffffff;
	--alert-bg-color: #dd3535;
}

/* Styles reset */

* {
	margin: 0;
	padding: 0;
}

h1,
h2,
h3,
h4,
h5,
img {
	margin: 0;
	padding: 0;
}

/* General styles */

body {
	font-family: 'Mulish', sans-serif;
}

h1 {
	font-size: 26;
}

h2 {
	font-size: 18px;
	margin: 5px 0 10px 0;
}

h3 {
	font-size: 14px;
	margin: 14px;
}

h4 {
	font-size: 12px;
	font-weight: 600;
	margin: 12px;
}

p {
	font-size: 12px;
}

b {
	font-weight: 600;
}

.disabled {
	color: var(--primary-bg-color-disabled);
}
.icon {
	width: 14px;
	height: 14px;
	margin: 0;
}

/* Classes */

.main {
	background-color: #ffffff;
	background-color: var(--main-bg-color);
	display: flexbox;
	width: 100vw - 6px;
	height: 100%;
	border-left: 3px solid #4f437c;
	border-left: 3px solid var(--primary-bg-color);
	border-right: 3px solid #4f437c;
	border-right: 3px solid var(--primary-bg-color);
}

.container {
	padding: 20px 30px;
}

.navbar {
	padding: 1em;
	margin-bottom: 20px;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	justify-content: center;
	align-items: center;
	z-index: 99999;
}

.navbar ul {
	list-style: none;
	padding: 0;
	margin: 0;
	background: var(--main-bg-color);
	justify-content: center;
}
.navbar li a {
	color: #333;
	display: block;
	font-size: 12px;
	text-decoration: none;
	text-transform: uppercase;
	padding: 0.7em 1em;
	text-align: center;
	border-radius: 5px;
}

.navbar li a:hover,
.search_button:hover {
	border: 2px solid var(--secondary-bg-color);
	margin-bottom: -2px;
}

.navbar .active {
	margin-bottom: -2px;
}

.navbar ul li img {
	margin-top: -6px;
	margin-bottom: -3px;
	height: 20px;
}

.search_button {
	width: 100%;
	padding: 0.9em 0.7em;
	margin-top: 8px;
	background: none;
	outline: none;
	border: none;
	cursor: pointer;
	border-radius: 5px;
}

.search_button.active {
	background-color: var(--secondary-bg-color);
}

.searchbar.active {
	display: flex;
	margin: 20px auto -30px auto;
	justify-content: space-between;
	border: 2px solid var(--secondary-bg-color);
	border-radius: 5px;
	box-shadow: 6px 6px 11px -5px rgba(109, 99, 168, 1);
	visibility: visible;
	opacity: 1;
	transition: opacity 600ms, visibility 600ms, box-shadow 600ms,
		margin-top 1000ms;
}

.searchbar.disabled {
	display: flex;
	margin: -10px auto -54px auto;
	justify-content: space-between;
	border: 2px solid var(--secondary-bg-color);
	border-radius: 5px;
	box-shadow: 6px 6px 11px -5px rgba(109, 99, 168, 1);
	opacity: 0;
	visibility: hidden;
	transition: opacity 400ms, visibility 400ms, box-shadow 400ms,
		margin-top 1000ms;
}

.searchbar input {
	display: block;
	height: 30px;
	width: 100%;
	padding: 5px 15px;
	border: none;
	border-radius: 5px;
}

.searchbar input:focus {
	outline: none;
}

.searchbar .search_result {
	height: 30px;
	width: 100%;
	padding: 5px 15px;
	border: none;
	border-radius: 5px;
}

.search_result {
	display: flex;
}

.searchbar .search_result input {
	color: white;
	background: var(--secondary-bg-color);
	height: 20px;
	width: 150px;
	cursor: pointer;
}

.searchbar .search_result img {
	height: 15px;
	margin-left: -30px;
	cursor: pointer;
}

.searchbar img,
.searchbar button {
	display: block;
	height: 25px;
	color: var(--secondary-bg-color);
	font-size: 20px;
	margin-top: 8px;
	margin-right: 10px;
	background: none !important;
	outline: none;
	border: none;
	cursor: pointer;
}

.searchbar button img {
	padding: 5px;
	width: 30px;
	margin-right: 12px;
	margin-top: -4px;
}

.searchbar .loading_image {
	width: 20px;
	height: 20px;
	margin-right: 22px;
	margin-top: 10px;
}

@media screen and (min-width: 630px) {
	nav ul {
		display: flex;
	}
	.navbar .active {
		background: var(--main-bg-color);
		border-bottom: 2px solid var(--secondary-bg-color);
	}

	.navbar li a {
		padding: 0.5em 1em;
		border-radius: 0;
	}

	.navbar li a:hover,
	.navbar button:hover {
		border: none;
		border-bottom: 2px solid var(--secondary-bg-color);
	}

	.search_button {
		padding: 0.5em 0.7em;
		margin-left: 6px;
		margin-top: -1px;
		border-radius: 0;
	}
	.searchbar {
		width: 40vw;
	}

	.searchbar button:hover {
		color: var(--primary-bg-color-disabled);
		border-bottom: none;
	}
}

.header {
	padding: 30px 0 0 0;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	border-top: 3px solid var(--primary-bg-color);
}

.header h1 {
	color: #434c54;
	text-transform: uppercase;
	font-size: 26px;
	margin-top: -10px;
	margin-bottom: 8px;
}

.footer {
	display: flex;
	flex-wrap: wrap;
	flex-direction: column;
	align-items: center;
	background: var(--primary-bg-color);
	padding: 20px;
}

.footer h4 {
	margin: 0;
	font-size: 30px;
	text-transform: uppercase;
	color: var(--secondary-txt-color);
}

.footer p {
	display: block;
}

.loader {
	display: flex;
	justify-content: center;
}
.lds-default {
	display: inline-block;
	position: relative;
	width: 80px;
	height: 80px;
}
.lds-default div {
	position: absolute;
	width: 6px;
	height: 6px;
	background: rgb(201, 73, 73);
	border-radius: 50%;
	-webkit-animation: lds-default 1.2s linear infinite;
	        animation: lds-default 1.2s linear infinite;
}
.lds-default div:nth-child(1) {
	-webkit-animation-delay: 0s;
	        animation-delay: 0s;
	top: 37px;
	left: 66px;
}
.lds-default div:nth-child(2) {
	-webkit-animation-delay: -0.1s;
	        animation-delay: -0.1s;
	top: 22px;
	left: 62px;
}
.lds-default div:nth-child(3) {
	-webkit-animation-delay: -0.2s;
	        animation-delay: -0.2s;
	top: 11px;
	left: 52px;
}
.lds-default div:nth-child(4) {
	-webkit-animation-delay: -0.3s;
	        animation-delay: -0.3s;
	top: 7px;
	left: 37px;
}
.lds-default div:nth-child(5) {
	-webkit-animation-delay: -0.4s;
	        animation-delay: -0.4s;
	top: 11px;
	left: 22px;
}
.lds-default div:nth-child(6) {
	-webkit-animation-delay: -0.5s;
	        animation-delay: -0.5s;
	top: 22px;
	left: 11px;
}
.lds-default div:nth-child(7) {
	-webkit-animation-delay: -0.6s;
	        animation-delay: -0.6s;
	top: 37px;
	left: 7px;
}
.lds-default div:nth-child(8) {
	-webkit-animation-delay: -0.7s;
	        animation-delay: -0.7s;
	top: 52px;
	left: 11px;
}
.lds-default div:nth-child(9) {
	-webkit-animation-delay: -0.8s;
	        animation-delay: -0.8s;
	top: 62px;
	left: 22px;
}
.lds-default div:nth-child(10) {
	-webkit-animation-delay: -0.9s;
	        animation-delay: -0.9s;
	top: 66px;
	left: 37px;
}
.lds-default div:nth-child(11) {
	-webkit-animation-delay: -1s;
	        animation-delay: -1s;
	top: 62px;
	left: 52px;
}
.lds-default div:nth-child(12) {
	-webkit-animation-delay: -1.1s;
	        animation-delay: -1.1s;
	top: 52px;
	left: 62px;
}
@-webkit-keyframes lds-default {
	0%,
	20%,
	80%,
	100% {
		transform: scale(1);
	}
	50% {
		transform: scale(1.5);
	}
}
@keyframes lds-default {
	0%,
	20%,
	80%,
	100% {
		transform: scale(1);
	}
	50% {
		transform: scale(1.5);
	}
}

.newsblocks {
	width: 100%;
	display: flex;
	flex-wrap: wrap;
	justify-content: space-around;
	margin-bottom: 20px;
}

.newsblocks h2 {
	width: calc(100% - 40px);
	margin-left: 0;
	background: var(--primary-bg-color);
	color: var(--primary-txt-color);
	margin-top: 0;
	margin-bottom: 15px;
	padding: 0.8em 0.5em;
	text-transform: uppercase;
	border-radius: 4px;
}

.newsblock {
	width: 40%;
	margin-bottom: 20px;
	text-align: center;
	overflow: hidden;
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	border-radius: 10px;
	box-shadow: 10px 10px 12px 0px rgba(230, 230, 230, 1);
}
.newsblock h4 {
	margin-top: 0;
	padding: 0.5em;
}
.newsblock img {
	width: 100%;
	max-height: 200px;
	background: linear-gradient(
		to right,
		var(--primary-bg-color) 0%,
		#f6f6f6 100%
	);
}

.newsblock a {
	padding: 5px 0.5em 5px 0.5em;
	text-decoration: none;
	background: var(--primary-bg-color);
	color: var(--primary-txt-color);
}

@media screen and (min-width: 630px) {
	.newsblock {
		width: 28vw;
		max-width: 250px;
	}
}

.newslist {
	width: 100%;
	padding: 10px;
	display: flex;
	flex-wrap: wrap;
	margin-bottom: 20px;
}

.newslist h2 {
	width: calc(100% - 40px);
	margin-left: 0;
	background: var(--primary-bg-color);
	color: var(--primary-txt-color);
	margin-top: 0;
	margin-bottom: 15px;
	padding: 0.5em;
	text-transform: uppercase;
	border-radius: 4px;
}

.newslist_item {
	width: 90%;
	height: 100px;
	margin: 20px auto;
	text-align: center;
	overflow: hidden;
	display: flex;
	justify-content: space-between;
	padding-bottom: 20px;
	border-bottom: 1px solid var(--primary-bg-color);
}

.newslist_item img {
	width: 30%;
	height: 100px;
	background: linear-gradient(
		to right,
		var(--primary-bg-color) 0%,
		#f6f6f6 100%
	);
	border-radius: 5px;
	box-shadow: -3px -3px 10px 6px rgba(230, 230, 230, 1);
}

.newslist_item a {
	padding: 5px 0.5em 5px 0.5em;
	text-decoration: none;
	color: var(--secondary-bg-color);
}

@media screen and (min-width: 630px) {
	.newslist_item {
		width: calc(50% - 40px);
		margin: 20px;
	}
}

.morenewsbutton {
	width: 40%;
	padding: 0 30%;
	margin-bottom: 30px;
}

div[class*='box'] {
	height: 33.33%;
	width: 100%;
	display: flex;
	justify-content: center;
	align-items: center;
}

.box-1 {
	background-color: var(--main-bg-color);
}

.btn {
	line-height: 50px;
	height: 50px;
	text-align: center;
	width: 100%;
	cursor: pointer;
}

/* 
========================
      BUTTON ONE
========================
*/
.btn-one {
	color: var(--secondary-bg-color);
	transition: all 0.3s;
	position: relative;
}
.btn-one span {
	transition: all 0.3s;
}
.btn-one::before {
	content: '';
	position: absolute;
	bottom: 0;
	left: 0;
	width: 100%;
	height: 100%;
	z-index: 1;
	opacity: 0;
	transition: all 0.3s;
	border-top-width: 1px;
	border-bottom-width: 1px;
	border-top-style: solid;
	border-bottom-style: solid;
	border-top-color: var(--secondary-bg-color);
	border-bottom-color: var(--secondary-bg-color);
	transform: scale(0.1, 1);
}
.btn-one:hover span {
	letter-spacing: 2px;
}
.btn-one:hover::before {
	opacity: 1;
	transform: scale(1, 1);
}
.btn-one::after {
	content: '';
	position: absolute;
	bottom: 0;
	left: 0;
	width: 100%;
	height: 100%;
	z-index: 1;
	transition: all 0.3s;
	background-color: rgba(214, 93, 93, 0.1);
}
.btn-one:hover::after {
	opacity: 0;
	transform: scale(0.1, 1);
}

.error {
	display: flex;
	justify-content: center;
}

.error img {
	margin-top: 20px;
	width: 400px;
}

.error_data {
	display: flex;
	flex-direction: column;
	justify-content: center;
}
.error_data a {
	display: block;
}

.error_data h2,
.error_data a {
	font-size: 18px;
	margin: 10px 10px;
}

.error_data a {
	text-decoration: none;
	color: var(--secondary-bg-color);
}

@media screen and (min-width: 630px) {
	.error img {
		margin-top: 50px;
		width: 200px;
	}
}

